import axios from "axios"

export default{
    getRate,
    getMarketPrice,
    getTransactionsPerDay
}

const BASE_URL='https://blockchain.info/tobtc?currency=USD&value=1'

async function getRate(coins){
   const dolar= await axios.get(BASE_URL)
   return dolar.data 
}
async function getMarketPrice(){
    const res=await axios.get('https://api.blockchain.info/charts/market-price?format=json&cors=true')
    return res.data.values
}
async function getTransactionsPerDay(){
    const res=await axios.get('https://api.blockchain.info/charts/n-transactions?format=json&cors=true')
    return res.data.values
}