import React from 'react';

import { getMarketPrice, getTransactionsPerDay } from '../actions'
import { connect } from 'react-redux'
import Chart from 'chart.js';



class StatisticPage extends React.Component {
    state = {
        isLoad: true,
        ctx: '',
        ctxT: '',
    }
    async componentDidMount() {
        await this.props.getMarketPrice()
        await this.props.getTransactionsPerDay()
        this.setState({ isLoad: false })
        this.setState({ ctx: this.refs.myChart })
        this.setState({ ctxT: this.refs.TChart })
    }
    render() {
        let { marketPrice, transactionsPerDay } = this.props
        let { ctx, ctxT,isLoad } = this.state
        if (ctx) new Chart(ctx, {
            type: 'bar',
            data: {
                labels: marketPrice.map(v => new Date(v.x * 1000).toLocaleDateString()),
                datasets: [{
                    label: 'BTC In USD',
                    data: marketPrice.map(v => v.y),
                    backgroundColor: '#41c3f9'
                }]
            }
        });
        if (ctxT) new Chart(ctxT, {
            type: 'bar',
            data: {
                labels: transactionsPerDay.map(v => new Date(v.x * 1000).toLocaleDateString()),
                datasets: [{
                    label: 'Transactions Per Day In USD',
                    data: transactionsPerDay.map(v => v.y),
                    backgroundColor: 'green'
                }]
            }
        });
        return (
            <div className="StatisticPage container page">
                {isLoad && <div className="loader"></div>}
                {marketPrice &&
                    <canvas ref="myChart" ></canvas>
                }
                {transactionsPerDay &&
                    <canvas ref="TChart" ></canvas>
                }
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        marketPrice: state.bitcoin.marketPrice,
        transactionsPerDay: state.bitcoin.transactionsPerDay

    }
}

const mapDispatchToProps = {
    getMarketPrice,
    getTransactionsPerDay
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(StatisticPage)