import React from 'react';
import MovesList from '../../user/components/MovesList'
import { getRate } from '../actions'
import { getUser,logOut } from '../../user/actions'
import { connect } from 'react-redux'
import Button from '@material-ui/core/Button';
// import AddIcon from '@material-ui/icons/';
class Home extends React.Component {
    logOut=async()=>{
        await this.props.logOut()
        this.props.history.push('/signup')
    }

    async componentDidMount() {
        const user = await this.props.getUser()
        if (!user) this.props.history.push('/signup')
        else {
            this.props.getRate()
        }
    }
    render() {
        let { user, rate } = this.props
        let ex = rate * user.coins
        return (
            <div className="HomePage ">
                {user && <div className="DataUserArea">
                    <div className="ContentDataUaer">
                        <h3>Hello {user.name}</h3>
                        <div>
                            <h4>Coins:</h4> {user.coins}$
                        </div>
                        <div>
                            <h4>BTC:</h4>  {ex.toFixed(8)}₿
                        </div>
                        <Button variant="contained" size="small" title="LogOut"  
                        onClick={this.logOut} >
                            LogOut
                        </Button>
                    </div>
                </div>}
                {user && <div className="container">
                    <MovesList moves={user.moves} />
                </div>}
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        rate: state.bitcoin.rate,
        user: state.user.user
    }
}

const mapDispatchToProps = {
    getRate,
    getUser,
    logOut
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Home)
