import React from 'react';

import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import ContactList from '../components/ContactList'
import ContactFilter from '../components/ContactFilter'
import { Link } from 'react-router-dom'
import { getContacts } from '../actions'
import { connect } from 'react-redux'

class ContactPage extends React.Component {
    state = {
        isLoad: true
    }
    handelFilter = async (value) => {
        let filterBy = { term: value }
        await this.props.getContacts(filterBy)
        this.setState({ isLoad: false })
    }
    async componentDidMount() {
        await this.props.getContacts()
        this.setState({ isLoad: false })
    }
    render() {
        let { contacts } = this.props
        let { isLoad } = this.state
        return (
            <div className="ContactPage container page">
                <ContactFilter callBack={this.handelFilter} />
                <Link className="AddLink" to={`/contact/edit`}>
                    <Fab size="small" color="primary" aria-label="add" title="Add Contact">
                        <AddIcon />
                    </Fab>
                </Link>
                {isLoad && <div className="loader"></div>}
                {contacts && !isLoad &&
                    <ContactList contacts={contacts} />
                }
            </div>

        )
    }
}
const mapStateToProps = (state) => {
    return {
        contacts: state.contact.contacts
    }
}

const mapDispatchToProps = {
    getContacts,
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ContactPage)