
import React from 'react';

import { Link } from 'react-router-dom'
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';

import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';


function ContactPreview(props) { 
  const img=props.contact.imgUrl? props.contact.imgUrl:`https://robohash.org/${props.contact.name}?set=set2`
  return (
    <Card className="ContactPreview">
      <CardActionArea>
        <CardMedia
          component="img"
          alt="Contemplative Reptile"
          height="240"
          image={img}
          title="Contemplative Reptile"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {props.contact.name}
          </Typography>
          <Typography gutterBottom variant="subtitle2" component="h4">
            Email:
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {props.contact.email}
          </Typography>
          <Typography gutterBottom variant="subtitle2" component="h4">
            Phone:
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {props.contact.phone}
          </Typography>
        </CardContent>
      </CardActionArea>
      {props.match &&
        <CardActions>
          <Link to={`/contact/edit/${props.contact._id}`}>
            <Button size="small" color="primary">
              <EditIcon />
            </Button>
          </Link>
          <Button size="small" color="primary">
            <DeleteIcon/>
          </Button>
        </CardActions>
      }

    </Card>

  );
}

export default ContactPreview;