import React from 'react';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';

import { signup } from '../actions'
import { connect } from 'react-redux'



class SignupPage extends React.Component {
    state = {
        user: {
            name: '',
            email: ''
        }
    }
    editContact = (ev, field) => {
        const { value } = ev.target
        this.setState(prevState => {
            return {
                user: {
                    ...prevState.user,
                    [field]: value
                }
            }
        })
    }
    handleForm = async (ev) => {
        ev.preventDefault()
        if (!this.state.user.name || !this.state.user.email) return
        await this.props.signup(this.state.user)
        this.props.history.push('/')
    }

    render() {
        return (
            <div className="SignupPage page">
                <div className="SignupArea">
                <h2>SignUp</h2>
                <div className="SignupControl">
                    <FormControl>
                        <InputLabel htmlFor="component-simple">Name</InputLabel>
                        <Input id="component-simple" onChange={(ev) => this.editContact(ev, 'name')} />
                    </FormControl>
                </div>
                <div className="SignupControl">
                    <FormControl >
                        <InputLabel htmlFor="component-simple">Email</InputLabel>
                        <Input id="component-simple" name="email" onChange={(ev) => this.editContact(ev, 'email')} />
                    </FormControl>
                </div>
                <Button variant="contained" className="SignupBtn" color="primary" onClick={this.handleForm} >
                    Sign up
                    </Button>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state) => {
    return {
        user: state.user.user
    }
}

const mapDispatchToProps = {
    signup
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SignupPage)