import React from 'react';

import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';

class TransferFund extends React.Component {
    state = {
        amount: 0
    }
    getAmount = (ev) => {
        const { value } = ev.target
        this.setState({ amount: +value })
    }
    render() {
        const { amount } = this.state
        return (
            <div className="TransferFund">
                <h3>Transfer to {this.props.contact.name}</h3>
                    <FormControl>
                    <InputLabel htmlFor="standard-adornment-amount">Amount</InputLabel>
                    <Input
                        id="standard-adornment-amount"
                        onChange={this.getAmount}
                        startAdornment={<InputAdornment position="start">$</InputAdornment>}
                    />
                    </FormControl>
                    <Button className="TransferBtn" color="primary" size="small" variant="contained" onClick={(ev) => this.props.TransferFund(ev, amount)} >
                        Send
                    </Button>
                    <div className="ChildList">

                {this.props.children}
                    </div>
            </div>
        )
    }
}
export default TransferFund
