import React from 'react';
import MoveList from '../../user/components/MovesList'
import ContactPreview from '../components/ContactPreview'
import TransferFund from '../components/TransferFund'
import Button from '@material-ui/core/Button';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';

import { Link } from 'react-router-dom'
import { loadContact } from '../actions'
import { addMove } from '../../user/actions'
import { connect } from 'react-redux'


class ContactDetails extends React.Component {

    transferFund = (ev, amount) => {
        ev.persist()
        ev.preventDefault()
        const move = {
            amount,
            to: this.props.contact
        }
        this.props.addMove(move)
    }
    async componentDidMount() {
        const { id } = this.props.match.params
        this.props.loadContact(id)
    }
    render() {        
        let { contact, user,match } = this.props
        return (
            <div className="ContactDetails container page">
                <Link to={`/contact`} className="BackLink">
                <Button  size="small" variant="contained"  >
                <ArrowBackIcon/>
                Back
                    </Button>
                </Link>
                {contact && <ul className="ContactPreviewTransfer">
                    <ContactPreview contact={contact} match={match} />
                    <TransferFund contact={contact} TransferFund={this.transferFund} >
                        <MoveList moves={user.moves.filter(move => move.to.name === contact.name)} />
                    </TransferFund>
                </ul>
                }

            </div>

        )
    }
}

const mapStateToProps = (state) => {
    return {
        contact: state.contact.currContact,
        user: state.user.user
    }
}

const mapDispatchToProps = {
    loadContact,
    addMove
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ContactDetails)