import React from 'react';
import { loadContact, saveContact } from '../actions'
import ContactPreview from '../components/ContactPreview'
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import SaveIcon from '@material-ui/icons/Save';

import { Link } from 'react-router-dom'

import { connect } from 'react-redux'


class ContactEdit extends React.Component {
    state = {
        contact: {
            name: '',
            email: '',
            phone: ''
        }
    }
    editContact = (ev, field) => {
        const { value } = ev.target
        this.setState(prevState => {
            return {
                contact: {
                    ...prevState.contact,
                    [field]: value
                }
            }
        })
    }
    handleForm = async (ev) => {
        ev.preventDefault()
        await this.props.saveContact(this.state.contact)
        // await ContactService.saveContact()
        this.props.history.push('/contact')

    }
    async componentDidMount() {
        const { id } = this.props.match.params
        if (id) {
            const { contact } = await this.props.loadContact(id)
            this.setState({ contact })
        }

    }
    render() {
        let { contact } = this.state


        return (
            <div className="ContactEdit container page">
                <Link to={`/contact`} className="BackLink">

                    <Button size="small" variant="contained"  >
                        <ArrowBackIcon />
                        Back
                    </Button>
                </Link>
                <div className="ContactEditForm">
                    <ContactPreview contact={contact} />
                    <div className="EditForm" >
                        <div className="EditControl">
                            <FormControl>
                                <InputLabel htmlFor="component-simple">Name</InputLabel>
                                <Input type="text"
                                    id="component-simple"
                                    value={contact.name}
                                    onChange={(ev) => this.editContact(ev, 'name')} />
                            </FormControl>
                        </div>
                        <div className="EditControl">
                            <FormControl >
                                <InputLabel htmlFor="component-simple">Email</InputLabel>
                                <Input type="email"
                                    id="component-simple"
                                    value={contact.email}
                                    name="email"
                                    onChange={(ev) => this.editContact(ev, 'email')} />
                            </FormControl>
                        </div>
                        <div className="EditControl">
                            <FormControl >
                                <InputLabel htmlFor="component-simple">Phone</InputLabel>
                                <Input type="tel"
                                    id="component-simple"
                                    value={contact.phone}
                                    name="tel"
                                    onChange={(ev) => this.editContact(ev, 'phone')} />
                            </FormControl>
                        </div>
                        <div className="EditFormBtn">
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={this.handleForm}
                            startIcon={<SaveIcon />}
                        >
                            Save
                         </Button>
                        </div>

                    </div>
                </div>


            </div>

        )
    }
}

const mapStateToProps = (state) => {
    return {
        contact: state.contact.currContact
    }
}

const mapDispatchToProps = {
    loadContact,
    saveContact
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ContactEdit)